body {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  margin: 0;
  overflow: hidden;
  font-family: "Lato", sans-serif;
  background-color: #4d394b;
}

.sudoku-container {
  border: 4px solid #1f171f;
  margin: 0 auto;
  position: relative;
  padding: 0 2px;
}
.sudoku-container::before,
.sudoku-container::after {
  position: absolute;
  border-style: solid;
  pointer-events: none;
  content: "";
}
.sudoku-container::before {
  width: 156px;
  border-width: 0 4px;
  border-color: transparent #1f171f transparent #1f171f;
  top: 4px;
  left: 156px;
  bottom: 4px;
}
.sudoku-container::after {
  height: 156px;
  border-width: 4px 0;
  border-color: #1f171f transparent #1f171f transparent;
  left: 4px;
  top: 156px;
  right: 4px;
}

tr:nth-child(1) td:nth-child(3) input,
tr:nth-child(1) td:nth-child(6) input {
  margin: 0 10px 0 0;
}

tr:nth-child(3) input,
tr:nth-child(6) input {
  margin: 0 0 10px 0;
}

.sudoku-container input {
  width: 40px;
  height: 40px;
  text-align: center;
  font-size: 20px;
  padding: 0;
  border: 3px #2e222d solid;
  background-color: #2e222d;
  color: #eee;
}
.sudoku-container input:focus {
  border-color: #4c9689;
}
.sudoku-container input.highlight {
  background-color: #29b6f6;
  border-color: #29b6f6;
}
.sudoku-container input.disabled {
  cursor: not-allowed;
  background-color: #4c9689;
  border-color: #4c9689;
}
.sudoku-container input.disabled.highlight {
  background-color: #29b6f6;
  border-color: #29b6f6;
}

.sudoku-container .invalid {
  border-color: #e74c3c;
}
.sudoku-container .invalid:focus {
  border-color: #e74c3c;
}

.sudoku-container.valid-matrix {
  border-color: #4c9689;
}

#controls {
  margin: 20px 0;
}

.btn.primary {
  background-color: #4c9689;
  box-shadow: inset 0 -3px #3b746a;
}

.btn.inActive {
  background-color: gray;
}

.btn,
.bttn {
  border: none;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  color: inherit;
  background: none;
  cursor: pointer;
  padding: 8px 12px;
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
  outline: none;
  position: relative;
  background: #95a5a6;
  color: #fff;
  box-shadow: inset 0 -3px #798d8f;
  border-radius: 3px;
}
button {
  appearance: auto;
  writing-mode: horizontal-tb !important;
  text-rendering: auto;
  /* color: buttontext; */
  letter-spacing: normal;
  word-spacing: normal;
  line-height: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: center;
  align-items: flex-start;
  cursor: default;
  box-sizing: border-box;
  /* background-color: buttonface; */
  margin: 0em;
  padding: 1px 6px;
  border-width: 2px;
  border-style: outset;
  border-color: buttonborder;
  border-image: initial;
}

.load-wrapp {
  float: left;
  width: 50px;
  height: 50px;
  margin: 0 10px 10px 0;
  padding: 20px 20px 20px;
  border-radius: 5px;
  text-align: center;
  /* background-color: #d8d8d8; */
}

.spinner {
  position: relative;
  width: 45px;
  height: 45px;
  margin: 0 auto;
}
.load-wrapp:last-child {
  margin-right: 0;
}
.load-9 .spinner {
  animation: loadingI 2s linear infinite;
}

.bubble-1,
.bubble-2 {
  position: absolute;
  top: 0;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  background-color: rgba(226, 125, 57, 0.832);
}
.bubble-2 {
  top: auto;
  bottom: 0;
}

.load-9 .bubble-1,
.load-9 .bubble-2 {
  animation: bounce 2s ease-in-out infinite;
}
.load-9 .bubble-2 {
  animation-delay: -1s;
}

@keyframes loadingI {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes bounce {
  0%,
  100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}

@media screen and (max-width: 500px) {
  .sudoku-container input {
    width: 27px;
    height: 27px;
  }
  .sudoku-container::before {
    width: 117px;
    left: 117px;
  }
  .sudoku-container::after {
    top: 117px;
    height: 117px;
  }
}

@media screen and (max-width: 400px) {
  .sudoku-container input {
    width: 20px;
    height: 20px;
  }
  .sudoku-container::before {
    width: 96px;
    left: 96px;
  }
  .sudoku-container::after {
    top: 96px;
    height: 96px;
  }
}
